.informacion{
    display: grid;
    grid-template-areas: 
    "riego riego img1 img1"
    "img2 img2 luz luz"
    "trans trans img3 img3"
    "img4 img4 abono abono";
    grid-template-columns: 2fr 2fr 2fr 2fr;
    grid-template-rows: auto auto auto auto;
}
.imformacion__riego{
    grid-area: riego;
    border: 2px solid black;
    margin: 2px;
}
.informacion__img1{
    grid-area: img1;
    border: 2px solid black;
    margin: 2px;
}
.informacion__img2{
    grid-area: img2;
    border: 2px solid black;
    margin: 2px;
}
.informacion__luz{
    grid-area: luz;
    border: 2px solid black;
    margin: 2px;
}
.informacion__transplante{
    grid-area: trans;
    border: 2px solid black;
    margin: 2px;
}
.informacion__img3{
    grid-area: img3;
    border: 2px solid black;
    margin: 2px;
}
.informacion__img4{
    grid-area: img4;
    border: 2px solid black;
    margin: 2px;
}
.informacion__abono{
    grid-area: abono;
    border: 2px solid black;
    margin: 2px;
}
.menu{
    max-height: 80px;
}
.menu img{
    border-radius: 100%;
    max-height: 65px;
}